
.content {
    padding: 0 148px;
    max-width: 1000px;
    margin: auto;
}

.hero {
    padding: 88px 0;
    display: inline-flex;
    flex-direction: row;
    gap: 40px;

    h1 {
        color: #000;
        font-family: Inter;
        font-size: 40px;
        font-style: normal;
        font-weight: 700;
        line-height: 60px;
        margin: 0 0 8px;
    }

    .hero-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        h4 {
            color: #4D4D4D;
            font-family: Inter;
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            margin: 0 0 24px;
        }

        p {
            color: #4D4D4D;
            margin: 0 0 40px;
        }
    }
}

h5 {
    color: #000;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}

.recent-presentations-wrapper {
    background: #E5F1EC;
    position: absolute;
    left: 0;
    right: 0;

    .recent-presentations {
        display: flex;
        padding: 88px 148px;
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
        background: #E5F1EC;
        margin: auto;
        max-width: 1000px;

        .presentation-cards {
            display: flex;
            width: 100%;
            align-items: flex-start;
            align-content: flex-start;
            gap: 28px;
            flex-wrap: wrap;
            flex-direction: row;
        }
    }
}

.recent-projects {
    display: flex;
    padding: 88px 0;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-top: 397px;
}

@media only screen and (max-width: 900px) {
    .content {
        padding: 0 40px;

        .hero img.avatar {
            display: none;
        }

        .recent-presentations-wrapper .recent-presentations {
            padding: 88px 40px;
        }
    }
}
